/**
 *  hh:mm:ss 
 *
 * @param {number} seconds 
 * @return {string} 
 */

/**
 * 
 *
 * @inner
 *
 * @param {string|number} val  1h 12h 23m 1s
 * @return {string} 
 */
function pad(val) {
    val = '' + val;
    if (val.length < 2) {
        val = '0' + val;
    }

    return val;
}

/**
 *  hh:mm:ss  mm:ss 
 *
 * @param {number} seconds 
 * @return {string} 
 */
export default function timeFormat(seconds) {
    seconds = parseInt(seconds, 10);
    if (!isNaN(seconds)) {
        let hour = Math.floor(seconds / 3600);
        let minute = Math.floor((seconds - hour * 3600) / 60);
        let second = seconds % 60;

        let result = [pad(minute), pad(second)];
        if (hour > 0) {
            result.unshift(pad(hour));
        }

        return result.join(':');
    } else {
        return '- -';
    }
}